.flex {
  display: flex;
  justify-content: space-between;
}

.view {
  margin-left: auto;
  margin-right: auto;
  width: 1024px;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto auto auto auto;
  border: 1px solid rgba(0, 0, 0, 0.8);
  background-color: rgba(255, 204, 153, 0.2);
}

.grid-item {
  display: inline-grid;
  border: 1px solid rgba(0, 0, 0, 0.8);
  /* width: 100/11; */
  /* padding: 20px; */
  /* text-align: center; */
  /* align-content: end; */
}

img {
  /* max-height: 50px; */
  /* width: 50px; */
  width: 90%;
  padding: 5%;
}

.buttons {
  /* text-align: center; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 30px 0;
}

button {
  font-size: 35px;
  font-family: inherit;
  padding: 20px 30px;
  background-color: transparent;
  border: 2px solid;
}

button:hover {
  background-color: rgba(255, 204, 153, 0.2);
}

.help-button {
  background-color: transparent;
  border: 0;
  width: 100px;
}

.help-button:hover {
  background-color: transparent;
}

.help-button:focus {
  outline: none;
}

.help-button:hover img {
  background-color: rgba(255, 204, 153, 0.2);
}

.help-button img {
  border-radius: 25px;
}

@media screen and (max-width: 1024px) {
  .view {
    margin-left: 0;
    margin-right: 0;
    width: auto;
  }
  button {
    width: 60%;
    margin: 10px;
    font-size: 12px;
    padding: 10px 30px;
  }
  .buttons {
    justify-content: center;
  }
  .help-button {
    margin: 0;
    padding: 0;
    max-width: 50px;
  }
}
